/* eslint-disable react-native/no-inline-styles */
/* eslint-disable react/no-unstable-nested-components */
import React, {useEffect} from 'react';
import {useState} from 'react';
import {Alert, Dimensions, View} from 'react-native';
import {ActivityIndicator, Button, Text, useTheme} from 'react-native-paper';
import {WebView} from 'react-native-webview';
import type {NativeStackScreenProps} from '@react-navigation/native-stack';
import Icon from 'react-native-vector-icons/MaterialCommunityIcons';
import {useSafeAreaInsets} from 'react-native-safe-area-context';
type RootStackParamList = {
  TestExamWebScreen: {uri: string};
};

type Props = NativeStackScreenProps<RootStackParamList, 'TestExamWebScreen'>;

const TestExamWebScreen = ({navigation, route}: Props) => {
  const insets = useSafeAreaInsets();
  const theme = useTheme();
  const {uri} = route.params;
  const [renderKey, setRenderKey] = useState(Date.now());
  const [error, setError] = useState(false);
  const [showURI, setShowURI] = useState(false);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    navigation.setOptions({
      headerShown: true,
      statusBarColor: theme.colors.primary,
      header: () => (
        <View
          style={{
            height: 20,
            width: Dimensions.get('window').width,
            paddingTop: insets.top,
            backgroundColor: theme.colors.primary,
            alignItems: 'flex-end',
          }}>
          <Button
            style={{
              height: 20,
            }}
            onPress={() => {
              Alert.alert(
                'Thông báo',
                'Bạn đang làm bài, bạn có chắc chắn muốn thoát!',
                [
                  {
                    text: 'Tiếp tục làm bài',
                    style: 'cancel',
                    onPress: () => {},
                  },
                  {
                    text: 'Thoát',
                    style: 'destructive',
                    // If the user confirmed, then we dispatch the action we blocked earlier
                    // This will continue the action that had triggered the removal of the screen
                    onPress: () =>
                      navigation.canGoBack()
                        ? navigation.goBack()
                        : navigation.navigate('Home' as any),
                  },
                ],
              );
            }}>
            <Icon size={22} name="close" color="#ffffff" />
          </Button>
        </View>
      ),
      gestureEnabled: false,
      // headerLeft: () => (
      //   <Button
      //     onPress={() => {
      //       Alert.alert(
      //         'Thông báo',
      //         'Bạn đang làm bài, bạn có chắc chắn muốn thoát!',
      //         [
      //           {
      //             text: 'Tiếp tục làm bài',
      //             style: 'cancel',
      //             onPress: () => {},
      //           },
      //           {
      //             text: 'Thoát',
      //             style: 'destructive',
      //             // If the user confirmed, then we dispatch the action we blocked earlier
      //             // This will continue the action that had triggered the removal of the screen
      //             onPress: () =>
      //               navigation.canGoBack()
      //                 ? navigation.goBack()
      //                 : navigation.navigate('Home' as any),
      //           },
      //         ],
      //       );
      //     }}>
      //     <Icon size={22} name="close" />
      //   </Button>
      // ),
      title: 'Làm bài',
    });
  }, [insets.top, navigation, theme.colors.primary]);

  if (!uri) {
    return (
      <View>
        <Text>Không tìm thấy bài thi!</Text>
      </View>
    );
  }

  if (error) {
    return (
      <View
        style={{
          flex: 1,
          padding: 24,
          gap: 20,
          justifyContent: 'center',
          alignItems: 'center',
        }}>
        <Text variant="bodyLarge" style={{textAlign: 'center'}}>
          Có lỗi xảy ra, vui lòng kiểm tra mạng và bấm nút "Tải lại trang"!
        </Text>
        {showURI === true && <Text style={{textAlign: 'center'}}>{uri}</Text>}
        <Button
          mode="contained"
          onLongPress={() => {
            setShowURI(!showURI);
          }}
          onPress={() => {
            setRenderKey(Date.now());
            setLoading(true);
            setError(false);
          }}>
          Tải lại trang
        </Button>
      </View>
    );
  }

  return (
    <View style={{flex: 1, paddingTop: insets.top, backgroundColor: '#fff'}}>
      {loading === true && (
        <View style={{padding: 20}}>
          <ActivityIndicator animating={true} />
        </View>
      )}
      <WebView
        javaScriptEnabled
        domStorageEnabled
        onError={() => {
          setError(true);
          return (
            <View>
              <Text>
                Có lỗi xảy ra, vui lòng kiểm tra mạng và bấm nút "Tải lại
                trang"!
              </Text>
            </View>
          );
        }}
        onLoad={() => setLoading(false)}
        key={renderKey}
        cacheEnabled={true}
        onReceivedError={() => {
          return (
            <View>
              <Text>
                Có lỗi xảy ra, vui lòng kiểm tra mạng và bấm nút "Tải lại
                trang"!
              </Text>
            </View>
          );
        }}
        startInLoadingState={true}
        // renderLoading={() => <ActivityIndicator animating={true} />}
        renderError={() => {
          return (
            <View>
              <Text>
                Có lỗi xảy ra, vui lòng kiểm tra mạng và bấm nút "Tải lại
                trang"!
              </Text>
            </View>
          );
        }}
        source={{uri}}
        style={{flex: 1}}
      />
    </View>
  );
};

export default TestExamWebScreen;
